body {
  background-color: rgb(136, 228, 124);
}

.Options {
  display: flex;
  justify-content: center;
  gap: 24px;

  &__item {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }

  &__input {
    padding: 12px 6px;
    border: 1px solid #cdcdcd;
    border-radius: 4px;
    outline: none;
    font-size: 14px;
    line-height: 16px;
    color: #666;
  }
}
